.submit-post-button {
  position: relative;
  height: 1.8rem;
  width: 5.5rem;
  background: transparent;
  display: flex;
  font-size: 0.7em;
  font-family: Impact, Haettenschweiler, "Arial Narrow Bold", sans-serif;
  color: rgba(150, 150, 150, 0.856);
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border: 0.1em solid rgba(163, 163, 163, 0.623);
  opacity: 1;
  -webkit-transition: opacity 0.5s;
  transition: opacity 0.5s;
}

.submit-post-button--valid {
  color: #969696;
  border: 0.1em solid #a3a3a3;
}

.submit-post-button--valid:before {
  content: "";
  background: -webkit-linear-gradient(45deg, #ff0000, #ff7300, #fffb00, #48ff00, #00ffd5, #002bff, #7a00ff, #ff00c8, #ff0000);
  background: linear-gradient(45deg, #ff0000, #ff7300, #fffb00, #48ff00, #00ffd5, #002bff, #7a00ff, #ff00c8, #ff0000);
  position: absolute;
  top: 2px;
  left: 1px;
  background-size: 400%;
  z-index: -1;
  -webkit-filter: blur(5px);
          filter: blur(5px);
  width: calc(98%);
  height: calc(90%);
  -webkit-animation: glowing 20s linear infinite;
          animation: glowing 20s linear infinite;
  opacity: 1;
  -webkit-transition: opacity 0.3s ease-in-out;
  transition: opacity 0.3s ease-in-out;
}

.submit-post-button--valid:after {
  z-index: -1;
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background: #1a1a1a;
  left: 0;
  top: 0;
}

.submit-post-button--valid:hover {
  cursor: pointer;
  color: #c7c7c7;
  border-color: #c7c7c7;
}

.submit-post-button--valid:active,
.submit-post-button--valid:focus {
  color: white;
  border-color: white;
}

.submit-post-button-disabled {
  opacity: 0;
  -webkit-transition: opacity 0.5s;
  transition: opacity 0.5s;
}

